<template>
<div id="form">
    <input id="input" @keyup.enter="sendMsg" type="text" v-model="inputVal" autocomplete="off" />
    <button @click="sendM">Enviar</button>
</div>
</template>

<script>
export default {
    data() {
        return {
            msg: '',
        }
    },
    props: {
        sendM: Function,
        inputValue: String
    },

    methods: {
        sendMsg() {
            this.sendM();
            this.inputVal = ''
        }
    },
    computed: {
        inputVal: {
            get() {
                return this.msg;
            },
            set(val) {
                this.msg = val
                this.$emit('changeInputValue', this.msg);
            }
        }
    }
}
</script>

